.FullPost {
    width: 80%;
    margin: 20px auto;
    border: 1px solid #eee;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    text-align: center;
}
.FullPost button {
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    color: #fa923f;
    font-size: large;
}
.FullPost button:hover,
.FullPost:active{
    color: #c55d08;
}
/* .Edit {

} */

.Edit label {
    display: block;
    color: grey;
}

.Edit button {
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    color: #fa923f;
}

.Edit button:hover,
.Edit button:active {
    color: #521751;
}

.Edit button.Delete {
    color: red;
    margin: 10px;
    font-size: large;
}

.Edit button.Delete:hover,
.Edit button.Delete:active {
    color: darkred;
}
.Edit button.Update {
    color: greenyellow;
    margin: 10px;
    font-size: large;
}

.Edit button.Update:hover,
.Edit button.Update:active {
    color: green;
}


